import React, { Component } from 'react'
import Faceithavewemet from '../assets/img/faceithavewemet.PNG'
import AnimalDetective from '../assets/img/animaldetective.PNG'

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faHtml5 } from '@fortawesome/free-brands-svg-icons';
import { faJsSquare } from '@fortawesome/free-brands-svg-icons';
import { faCss3Alt } from '@fortawesome/free-brands-svg-icons';







export class InnerGallery extends Component {



    
    render() {

        let Work =(props) => {
            return (
            <div className={props.classNamex}>
                        <div className="my-5 d-flex flex-column flex-wrap">
                            <div className="d-flex flex-wrap justify-content-center flex-row">
                                <div className="d-flex imgcontainer w-50 p-5">
                                    <img className="rounded border" alt={props.imageAlt} src={props.image}></img>
                              
                                </div>
                                <div className="d-flex flex-column  w-50 p-5">
                                    <h5 className="font-weight-bolder">{props.title}</h5>
                                    <span>
                                        <FontAwesomeIcon icon={props.tech1}/>   {" "}
                                        <FontAwesomeIcon icon={props.tech2}/>   {" "}
                                        <FontAwesomeIcon icon={props.tech3}/>   {" "}
                                        <FontAwesomeIcon icon={props.tech4}/>   {" "}
                                        </span>
                                    <p>{props.info}</p>
                                <a href={props.viewCode}  target="_blank" rel="noopener noreferrer">View Code</a>
                                <a href={props.viewProject} target="_blank" rel="noopener noreferrer">View Project</a>
                                </div>
                            </div>
                        </div>
                    </div>
          )
        }
        


        return (
            <div className="container w-100">
                    <div className="w-100 h-auto pt-5 my-5">
                        <h4 className="text-center">I'm currently looking for new projects!</h4>
                        <p  className="text-center mb-5">Here are some recent ones.</p>

                        <Work 
                        classNamex="h-auto border-bottom"
                        title="Animal Detective" 
                        image={AnimalDetective} 
                        tech1={faHtml5}
                        tech2={faJsSquare}
                        tech3={faCss3Alt }
                        tech4=""
                        imageAlt="Animal detective example image"
                        info="This website is used for lost and found pets. In czech called 'ZvireciDetektiv' will be launched early october 2019!" 
                        viewCode="https://github.com/AngeloAlfanoc/detective"
                        viewProject="https://angeloalfanoc.github.io/detective/"
                        />


                        <Work 
                         classNamex="h-auto"
                        title="Faceit have we met?" 
                        image={Faceithavewemet} 
                        tech1={faHtml5}
                        tech2={faJsSquare}
                        tech3={faCss3Alt }
                        tech4=""
                        imageAlt="Faceit have we met example image"
                        info="The app allows users to find matches played with each other." 
                        viewCode="https://github.com/AngeloAlfanoc/FaceitHaveWeMet"
                        viewProject="https://angeloalfanoc.github.io/FaceitHaveWeMet/"
                        />
                    </div>
            </div>

            
        )
    }
    
}

export default InnerGallery



